.button {
  width: 280px;
  padding-top: 14px;
  padding-bottom: 12px;

  @include font(16px, 20px, 400);
  text-transform: uppercase;
  color: white;

  &--green {
    background-color: $main-color;

    &:hover {
      background-color: #5eaa2f;
    }

    &:active {
      background-color: #5eaa2f;
      color: #8fc46e;
    }
  }

  &--grey {
    color: #444444;
    background-color: #f2f2f2;

    &:hover {
      background-color: #ebebeb;
    }

    &:active {
      background-color: #ebebeb;
      color: #b9b9b9;
    }
  }
  border: none;
}
